/* eslint-disable no-underscore-dangle */

'use strict';

// MAIN //

/**
* Writes a message to the REPL output stream.
*
* ## Notes
*
* -   Automatically appends a newline character to a provide message.
*
* @private
* @param {REPL} repl - REPL instance
* @param {string} msg - message
*/
function log( repl, msg ) {
	if ( repl._quiet === false ) {
		repl._ostream.write( msg+'\n' );
	}
}


// EXPORTS //

module.exports = log;
