'use strict';

// MODULES //

var aliases = require( '@stdlib/namespace/aliases' );
var comparator = require( './property_comparator.js' );


// MAIN //

// NOTE: we assume all aliases are unique (no duplicates!)
var ALIASES = aliases().sort( comparator );


// EXPORTS //

module.exports = ALIASES;
