'use strict';

// MODULES //

var typeOf = require( '@stdlib/utils/type-of' );


// MAIN //

/**
* Converts an `alias` argument to a string.
*
* @private
* @param {*} alias - alias
* @returns {string} string representation
*/
function alias2string( alias ) {
	if ( typeof alias === 'string' ) {
		return alias;
	}
	return '<' + typeOf( alias ) + '>';
}


// EXPORTS //

module.exports = alias2string;
